import type {LinksFunction, ActionFunction} from 'remix';
import {Link, useSearchParams, useActionData} from 'remix';
import {db} from '~/utils/db.server';
import stylesUrl from '../styles/login.css';

export let links: LinksFunction = () => {
  return [{rel: 'stylesheet', href: stylesUrl}];
};

function validateUsername(username: unknown) {
  if (typeof username !== 'string' || username.length < 3) {
    return `Usernames must be at least 3 characters long`;
  }
}

function validatePassword(password: string) {
  if (typeof password !== 'string' || password.length < 6) {
    return `Passwords must be at least six characters long`;
  }
}

type ActionData = {
  formError?: string;
  fieldErrors?: {
    username: string | undefined;
    password: string | undefined;
  };
  fields?: {
    loginType: string;
    username: string;
    password: string;
  };
};

export let action: ActionFunction = async ({
  request,
}): Promise<Response | ActionData> => {
  let form = await request.formData();
  let loginType = form.get('loginType');
  let username = form.get('username');
  let password = form.get('password');
  let redirectTo = form.get('redirectTo');

  // we do this type check to be extra sure and to make TypeScript happy
  if (
    typeof loginType !== 'string' ||
    typeof username !== 'string' ||
    typeof password !== 'string' ||
    typeof redirectTo !== 'string'
  ) {
    return {formError: `Form not submitted correctly.`};
  }

  let fieldErrors = {
    username: validateUsername(username),
    password: validatePassword(password),
  };

  let fields = {username, password, loginType};

  if (Object.values(fieldErrors).some(Boolean)) {
    return {fieldErrors, fields};
  }

  switch (loginType) {
    case 'login': {
      // login to get the user
      // if there's no user, return the fields and a formError
      // if there is a user, create their session and redirect to /jokes
      return {fields, formError: 'Not implemented'};
    }
    case 'register': {
      let userExists = await db.user.findFirst({
        where: {username},
      });
      if (userExists) {
        return {
          fields,
          formError: `User with username ${username} already exists`,
        };
      }
      // create the user
      // create their session and redirect to /jokes
      return {fields, formError: 'Not implemented'};
    }
    default: {
      return {fields, formError: 'Login type invalid'};
    }
  }

  // return redirect
};

export default function Login() {
  let actionData = useActionData<ActionData | undefined>();
  let [searchParams] = useSearchParams();
  return (
    <div className="container">
      <div className="content" data-light="">
        <h1>Login</h1>
        <form
          method="post"
          aria-describedby={
            actionData?.formError ? 'form-error-message' : undefined
          }
        >
          <input
            type="hidden"
            name="redirectTo"
            value={searchParams.get('redirectTo') ?? undefined}
          />
          <fieldset>
            <legend className="sr-only">Login or Register?</legend>
            <label>
              <input
                type="radio"
                name="loginType"
                value="login"
                defaultChecked
              />{' '}
              Login
            </label>
            <label>
              <input type="radio" name="loginType" value="register" /> Register
            </label>
          </fieldset>
          <div>
            <label htmlFor="username-input">Username</label>
            <input type="text" id="username-input" name="username" />
          </div>
          <div>
            <label htmlFor="password-input">Password</label>
            <input id="password-input" name="password" type="password" />
          </div>
          <button type="submit" className="button">
            Submit
          </button>
        </form>
      </div>
      <div className="links">
        <ul>
          <li>
            <Link to="/">Home</Link>
          </li>
          <li>
            <Link to="/jokes">Jokes</Link>
          </li>
        </ul>
      </div>
    </div>
  );
}
